/**
 * Site header
 */
.site-header {
    min-height: 56px;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;

    // Positioning context for the mobile navigation icon
    position: relative;
}

.title-logo {
    width: 4rem;
}

.site-title {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5;
    letter-spacing: -1px;
    margin-bottom: 0;
    float: left;
    text-decoration: none!important;
    font-family: 'armstrongbold', sans-serif;
    letter-spacing: 0.05rem;
    @include media-query($on-palm) {
        font-size: 1.3rem;
    }
    &, &:hover, 
    &:visited {
        color: #ffffff;
        text-decoration: none;
    }
}

.site-nav {
    float: right;
    line-height: 56px;

    .menu-icon {
        display: none;
    }

    .page-link {
        color: $text-color;
        line-height: $base-line-height;

        // Gaps between nav items, but not on the last one
        &:not(:last-child) {
            margin-right: 20px;
        }
    }

    @include media-query($on-palm) {
        position: absolute;
        top: 18px;
        right: $spacing-unit / 2;
        background-color: $background-color;
        border-radius: 5px;
        text-align: right;

        .menu-icon {
            display: block;
            float: right;
            width: 36px;
            height: 26px;
            line-height: 0;
            padding-top: 10px;
            text-align: center;

            > svg {
                width: 18px;
                height: 15px;

                path {
                    fill: $grey-color-dark;
                }
            }
        }

        .trigger {
            clear: both;
            display: none;
        }

        &:hover .trigger {
            display: block;
            padding-bottom: 5px;
        }

        .page-link {
            display: block;
            padding: 5px 10px;

            &:not(:last-child) {
                margin-right: 0;
            }
            margin-left: 20px;
        }
    }
}



/**
 * Site footer
 */
.site-footer {
    padding: 5rem 0;
    text-align: center;
    @include media-query($on-palm) {
        padding: 3rem 0;
    }
}

.footer-heading {
    font-size: 18px;
    margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
    list-style: none;
    margin-left: 0;
}

.footer-col-wrapper {
    font-size: 15px;
    text-align: center;
    color: $grey-color;
    @extend %clearfix;
    .links {
        ul {
            list-style: none;
            text-align: center;
            margin-left: 0;
            li {
                display: inline;
                margin-left: 1rem;
                margin-right: 1rem;
                @media only screen and (max-width: 75rem) {
                   margin-left: 0.5rem;
                   margin-right: 0.5rem; 
                }
            }
            
        }
    }
}



/**
 * Page content
 */
.page-content {
    padding: $spacing-unit 0;
    @media screen and (max-width: 40rem) {
        padding-top: 2rem;
    }
}

.page-heading {
    font-size: 20px;
}

.post-list {
    margin-left: 0;
    list-style: none;

    > li {
        margin-bottom: $spacing-unit;
    }
}

.post-meta {
    font-size: $small-font-size;
    color: $grey-color;
}

.post-link {
    display: block;
    font-size: 24px;
}



/**
 * Posts
 */
.post-header {
    margin-bottom: 1.3rem;
    @include media-query($on-laptop) {
        margin-bottom: 1rem;
    }
}

.post-title {
    font-size: 2.5rem;
    letter-spacing: -1px;
    line-height: 1.3;

    @include media-query($on-laptop) {
        font-size: 1.8rem;
    }
}

.post-content {

    h2 {
        font-size: 32px;

        @include media-query($on-laptop) {
            font-size: 1.3rem;
        }
    }

    h3 {
        font-size: 26px;

        @include media-query($on-laptop) {
            font-size: 22px;
        }
    }

    h4 {
        font-size: 20px;

        @include media-query($on-laptop) {
            font-size: 18px;
        }
    }
}
