/**
* Custom 
**/

a, button, input, select, textarea, .burger, label {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
}

$font-color: #7A7572;

.accordion {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.toggle {
  display: none;
}

.option {
  position: relative;
  margin-bottom: 1.5rem;
}

.title,
.content {
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: all 0.2s;
}

.title {
  background: #d1553c;
  padding: 1rem;
  display: block;
  color: #ffffff;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
}
.title:after, .title:before {
  content: '';
  position: absolute;
  right: 2rem;
  top: 1.7rem;
  width: 4px;
  height: 1.7rem;
  background-color: #ffffff;
  transition: all 0.2s;
    @include media-query($on-palm) {
        top: 1.4rem;
    }
}
.title:after {
  transform: rotate(90deg);
}

.content {
  max-height: 0;
  overflow: hidden;
  p {
    margin: 0;
    font-size: 1rem;
    line-height: 1.5;
  }
}

.toggle:checked {
  & + .title,
  & + .title + .content {
    
  }
  & + .title + .content {
    max-height: 100%;
  }
  & + .title:before {
    transform: rotate(90deg) !important;
  }
}

.section {
    padding: 6rem 1rem;
    @include media-query($on-palm) {
        padding: 3rem 0.5rem;
    }
    .row {
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }
    .col {
        -ms-flex-preferred-size: 70%;
        flex-basis: 70%;
        max-width: 70%;
        @media screen and (max-width: 62rem) {
            -ms-flex-preferred-size: 85%;
            flex-basis: 85%;
            max-width: 85%;
        }
        @include media-query(48rem) {
            -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
            max-width: 100%;
        }
        h2 {
            font-family: 'armstrongregular', 'Source Sans Pro', sans-serif;
            font-size: 3rem;
            text-align: center;
            @include media-query($on-palm) {
                font-size: 2.3rem;
            }
        }
        img {
            margin-top: 1rem;
            width: 800px;
            margin-left: auto;
            margin-right: auto;
        }
        p {
            font-size: 1.5rem;
            line-height: 1.7;
            @include media-query($on-palm) {
            font-size: 1.3rem;
        }
        }
    }
    label.title {
        font-size: 1.5rem;
        padding: 1.5rem;
        background-color: #d3622f;
        @include media-query($on-palm) {
            font-size: 1.3rem;
            padding: 1.3rem;
        }
    }
}

.section.dc-schedule, .section.badge {
    .col {
        -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
        max-width: 100%;
    }
}

.badge {
    h3 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-top: 2rem;
    text-align: center;
    }
    h3+p {
        text-align: center;
    }
}

.section .badge-shops {
    text-align: center;
    a {
        display: inline-block;
        @media screen and (max-width: 540px) {
            display: block;
        }
    }
    img.hak {
        height: 100px;
        width: auto;
        margin-right: 2rem;
        @media screen and (max-width: 540px) {
            margin-right: 0;
            margin-bottom: 2rem;
        }
    }
    img.cypher {
        height: 64px;
        width: auto;
        margin-left: 2rem;
        @media screen and (max-width: 540px) {
            margin: 0;
        }
    }
}

/* Event Styling */

.event {
    margin: 0;
    padding: 1rem;
    .event-time {
        margin-bottom: 0.5rem;
        font-size: 1.1rem;
        display: inline-block;
        padding: 0.3rem 0.5rem;
        font-family: 'Raleway', sans-serif;
        letter-spacing: 0.05rem;
        border-radius: 2px;
        color: #02041b;
    }
    .event-time.workshop {
        background-color: #f9da58;
        color: #02041b;
    }
    .event-time.activity {
        background-color: #5fd9bd;
        color: #02041b;
    }
    .event-time.speech {
        background-color: #b4ef0e;
        color: #02041b;
    }
    .event-time.film {
        background-color: #65a2d4;
        color: #02041b;
    }
    p.event-title {
        font-weight: 600;
        font-size: 1.5rem;
        margin-bottom: 0.5rem;
        @include media-query($on-palm) {
            font-size: 1.3rem;
        }
    }
    .event-speaker {
        margin-bottom: 0.5rem;
        font-size: 0.9rem;
    }
    .event-about {
        color: #797979;
    }
    .event-about span {
        font-weight: 600;
    }
    .event-about.space {
        padding-top: 0.5rem;
    }
}

.event:first-of-type {
    padding-top: 2rem;
}

/* LANDING PAGE */

.hero {
    @include media-query($on-palm) {
        
    }
    padding: 20rem 1rem 14rem 1rem;
    @media only screen and (max-width: 62rem) {
        padding-top: 8rem;
        padding-bottom: 6rem;
    }
    @include media-query($on-palm) {
         padding-bottom: 3rem;
    }
    h1 {
        font-size: 4.5rem;
        margin-bottom: 0;
        @media only screen and (max-width: 75rem) {
            font-size: 3.7rem;
        }
        @media only screen and (max-width: 62rem) {
            text-align: center;
            margin-top: 1rem;
        }
        @include media-query($on-palm) {
            font-size: 2.5rem;
        }
        @media only screen and (max-width: 360px) {
            font-size: 2.2rem;
        }
    }
    img {
        width: 100%;
    }
    .col:first-child {
        -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
        max-width: 50%;
        @media only screen and (max-width: 62rem) {
            -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
            max-width: 100%;
        }
    }
    .col:last-child {
        -ms-flex-preferred-size: 45%;
        flex-basis: 45%;
        max-width: 45%;
        @media only screen and (max-width: 62rem) {
            -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
            max-width: 100%;
            -ms-flex-order: -1;
            -webkit-box-ordinal-group: 0;
            order: -1;
        }
    }
}



.section.info {
    p:last-child {
        margin-bottom: 0;
    }
}

.section.location {
    p {
        text-align: center;
    }
}

.section.sponsors {
    text-align: center;
    img#globee-img {
        width: 15rem;
        margin-right: 2rem;
        @include media-query($on-palm) {
            margin: 0;
        }
    }
    img#tari-img {
        width: 12rem;
        margin-left: 2rem;
        @media screen and (max-width: 650px) {
            margin: 1.5rem 0 0 0;
            width: 9rem;
        }
    }
    a {
        display: inline-block;
        @media screen and (max-width: 650px) {
            display: block;
        }
    }
}

/* Nav*/

header {
    width: 100%;
    z-index: 100;
    position: absolute;
    top: 0;
}

.desktop-nav {
    line-height: 1;
    padding: 1rem 1rem 1rem 2rem;  
    box-sizing: border-box;
    -ms-flex: 0 0 auto;
    -webkit-box-flex: 0;
    flex: 0 0 auto;
    -ms-flex-preferred-size: 100%;
    flex-basis: 100%;
    max-width: 100%;
    .row {
        -ms-flex-align: center;
        -webkit-box-align: center;
        align-items: center;
    }
    .nav-items {
        .nav-item {
            li {
                display: inline-block;
                position: relative;
                a {
                padding: 1rem;
                color: #ffffff;
                text-decoration: none;
                font-size: 1.2rem;
                font-weight: 600;
                -webkit-transition: all ease-out .2s;
                -moz-transition: all ease-out .2s;
                -o-transition: all ease-out .2s;
                transition: all ease-out .2s;
            }
        }
        }
    }
    .col-sm-4 {
        box-sizing: border-box;
        -ms-flex: 0 0 auto;
        -webkit-box-flex: 0;
        flex: 0 0 auto;
        -ms-flex-preferred-size: 40%;
        flex-basis: 40%;
        max-width: 40%;
    }
    .col-sm-8 {
        box-sizing: border-box;
        -ms-flex: 0 0 auto;
        -webkit-box-flex: 0;
        flex: 0 0 auto;
        -ms-flex-preferred-size: 60%;
        flex-basis: 60%;
        max-width: 60%;
        .row {
            -ms-flex-pack: end;
            -webkit-box-pack: end;
            justify-content: flex-end;
            text-align: right;
        }
    }
}

.nav-items .nav-item>a:hover, .nav-items .nav-item>a:focus, .nav-items .nav-item>a:active, .nav-items .nav-item>label:hover, .nav-items .nav-item>label:focus, .nav-items .nav-item>label:active {
    color: #d4553c;
    text-decoration: none;
}

.nav-item li ul.dropdown {
    background: #02041b;
	visibility: hidden;
    opacity: 0;
    min-width: 15rem;
	position: absolute;
    transition: all 0.5s ease;
    margin-top: 1rem;
	left: 0;
    display: none;
    margin-left: 0;
    text-align: left; 
    li {
        width: 100%;
        a {
            display: block;
            padding: 1rem;
            -webkit-transition: background-color 500ms ease-out 1s;
            -moz-transition: background-color 500ms ease-out 1s;
            -o-transition: background-color 500ms ease-out 1s;
            transition: background-color 500ms ease-out 1s;
        }
        a:hover {
            background-color: rgba(241,106,36,.3);
        }
    }
}

.nav-item li:hover > ul.dropdown,
.nav-item li:focus-within > ul.dropdown,
.nav-item li ul.dropdown:hover,
.nav-item li ul.dropdown:focus {
   visibility: visible;
   opacity: 1;
   display: block;
}

.burger-check, .burger-checkdropdown {
	display: none;
}

@media only screen and (max-width: 62rem) {
    
.desktop-nav {
    display: none;
}
    
}

/*MOBILE NAVIGATION*/

.mobile-nav {
    display:none;
}

.mob.bot-nav {
    display: none;
}

@media only screen and (max-width: 62rem) {
  
.hamburger {
  padding: 0.8rem 1rem;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
    .hamburger-box {
      display: inline-block;
      position: relative;
      svg {
        display: inline-block;
        width: 1.5rem;
        height: 1.5rem;
        vertical-align: middle;
      }
      }
}
    
.burger-check:checked ~ .nav-items{
    display: block;
}

.burger-check:checked + label {
    position: fixed;
} 
    
.nav-item > label {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
}
    
.mobile-nav {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    background-color: #1a1818;
    padding: 1rem;
    -moz-box-shadow: 0 2px 2px rgba(0,0,0,.1);
    -webkit-box-shadow: 0 2px 2px rgba(0,0,0,.1);
    box-shadow: 0 2px 2px rgba(0,0,0,.1);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    .site-title {
        padding-right: 6rem;
    }
}
    
.slide-in-nav {
    text-align: center;
    position: absolute;
    top: 99%;
    width: 100%;
    transition: all ease .6s;
    background-color: #1a1818;
    color: #f7f7f7;
    height: 0;
    -webkit-transition: height 500ms ease;
    -moz-transition: height 500ms ease;
    -o-transition: height 500ms ease;
    transition: height 500ms ease;
    overflow: hidden;
    opacity: -1;
    z-index: 100;
    box-shadow: 0 4px 5px rgba(0,0,0,0.25);
    padding-top: 1rem;
    left: 0;
}
    
.slide-in-nav .row {
    height: auto;
    -ms-flex-pack: center;
    -webkit-box-pack: center;
    justify-content: center;
}
    
.slide-in-nav .col-xs-12 {
    padding: 0;
}
    
.slide-in-nav::-webkit-scrollbar { 
    display: none; 
}
    
label[for="mobile-burger"] {
    position: fixed;
    top: 0.15rem;
    right: 0rem;
    z-index: 11;
}
    
#mobile-burger:checked ~ .slide-in-nav {
    height: 100vh;
    opacity: 1;
}
    
#mobile-burger:checked ~ body {
    overflow: none;
}
    
.mob-nav .nav-item {
    text-align: center;
    font-size: 1rem;
    cursor: pointer;
}
    
.mob-nav .nav-item:checked, .mob-nav .nav-item:focus, .mob-nav .nav-item:active {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
}
   
.mob-nav .nav-item>a, .mob-nav .nav-item>label {
    text-decoration: none;
    color: #f2f2f2;
    cursor: pointer;
    line-height: 2.1rem;
    padding: 1.3rem 0;
    display: block;
    -webkit-transition: all .15s ease;
    -moz-transition: all .15s ease;
    -o-transition: all .15s ease;
    transition: all .15s ease;
    font-size: 1.3rem;
    font-weight: 600;
} 
    
.mob-nav .nav-item>a:hover, .mob-nav .nav-item>a:focus, .mob-nav .nav-item>a:active, .mob-nav .nav-item>a.active, .mob-nav .nav-item label:hover, .mob-nav .nav-item label:focus {
    color: #08BA07; 
}
    
.nav-item>.dropdown-content>a:hover {
    background-color: transparent;
} 
    
.burger-check:checked + label {
    position: fixed;
}
    
.logo-wrap {
    padding: 00;
}
    
img.logo {
    width: 6rem;
}
    
}


@media only screen and (max-width: 40rem) {

.mob-nav .nav-item>a, .mob-nav .nav-item>label {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1.2rem;
}
    
}

img.badge-pic {
    border-radius: 10px;
}

html {
  scroll-behavior: smooth;
}